* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

body {
    background: linear-gradient(135deg, #f5f7fa 0%, #e4e8f0 100%);
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    padding: 20px;
}

.form-container {
    background: white;
    padding: 2.5rem;
    border-radius: 12px;
    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.08);
    width: 100%;
    max-width: 600px;
    margin: 0 auto;
    border: 1px solid rgba(0, 0, 0, 0.05);
}

h2 {
    text-align: center;
    color: #2c3e50;
    margin-bottom: 1.8rem;
    font-size: 1.8rem;
    font-weight: 600;
    position: relative;
}

h2::after {
    content: '';
    display: block;
    width: 60px;
    height: 4px;
    background: #5c6bc0;
    margin: 12px auto 0;
    border-radius: 2px;
}

.form-group {
    margin-bottom: 1.5rem;
}

label {
    display: block;
    font-size: 0.95rem;
    color: #4a5568;
    margin-bottom: 0.6rem;
    font-weight: 500;
}

input[type="text"] {
    width: 100%;
    padding: 0.8rem 1rem;
    font-size: 0.95rem;
    border: 1px solid #e2e8f0;
    border-radius: 6px;
    margin-bottom: 1rem;
    background-color: #f8fafc;
    transition: all 0.25s ease;
}

input[type="text"]:focus {
    border-color: #5c6bc0;
    outline: none;
    background-color: #fff;
    box-shadow: 0 0 0 3px rgba(92, 107, 192, 0.15);
}

.input-group {
    display: flex;
    gap: 8px;
    margin-bottom: 8px;
    align-items: center;
}

.input-group input {
    flex: 1;
    margin-bottom: 0;
}

.button-icon {
    width: 36px;
    height: 36px;
    padding: 0;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    border-radius: 6px;
    font-size: 1.1rem;
    transition: all 0.2s ease;
    cursor: pointer;
    border: none;
}

.button-icon:hover {
    transform: scale(1.05);
}

.add-button {
    background-color: #48bb78;
    color: white;
}

.add-button:hover {
    background-color: #38a169;
}

.remove-button {
    background-color: #f56565;
    color: white;
}

.remove-button:hover {
    background-color: #e53e3e;
}

.submit-button {
    background-color: #5c6bc0;
    color: white;
    font-size: 1rem;
    padding: 12px;
    border: none;
    border-radius: 6px;
    cursor: pointer;
    transition: all 0.3s ease;
    width: 100%;
    margin-top: 1rem;
}

.submit-button:hover {
    background-color: #3f4a8a;
    transform: translateY(-2px);
}

@media (max-width: 768px) {
    .form-container {
        padding: 1.5rem;
    }

    h2 {
        font-size: 1.5rem;
    }
}

@media (max-width: 480px) {
    .form-container {
        padding: 1rem;
    }

    .input-group {
        flex-direction: column;
        gap: 4px;
    }

    .input-group input {
        width: 100%;
    }

    .button-icon {
        width: 100%;
        height: 40px;
    }
}